<template>
  <div class="col-md-3 side-bar">
    <!-- 轮播图 -->
    <div class="panel panel-default corner-radius sidebar-resources">
      <div class="panel-heading text-center">
        <h3 class="panel-title">推荐阅读</h3>
      </div>
      <div class="panel-body">
        <Slider :slides="slides">
          <template slot-scope="{ currentSlide }">
            <a :href="currentSlide.link" target="_blank">
              <img :alt="currentSlide.title" :title="currentSlide.title" :src="currentSlide.img">
            </a>
          </template>
        </Slider>
      </div>
    </div>
    <!-- 活跃用户 -->
    <div class="panel panel-default corner-radius panel-active-users">
      <div class="panel-heading text-center">
        <h3 class="panel-title">活跃用户</h3>
      </div>
      <div class="panel-body">
        <div class="users-label">
          <router-link v-for="(user, index) in activeUsers" :to="`/${user.name}`" :key="index" class="users-label-item">
            <img :src="user.avatar?user.avatar:userIconUrl" class="avatar-small inline-block">
            {{ user.name }}
          </router-link>
        </div>
      </div>
    </div>
    <!-- 七天内最热 -->
   <!--  <div class="panel panel-default corner-radius panel-hot-topics">
      <div class="panel-heading text-center">
        <h3 class="panel-title">七天内最热</h3>
      </div>
      <div class="panel-body">
        <ul class="list">
          <li v-for="(article, index) in hotArticles">
            <router-link :to="`/articles/${article.articleId}/content`">
              <span v-if="index === 0">?</span>
              <span v-else>{{ index + 1 }}.</span>
              {{ article.title }}
            </router-link>
          </li>
        </ul>
      </div>
    </div> -->
    <!-- 其他内容 -->
    <div class="other">
      <div class="panel panel-default corner-radius sidebar-resources">
        <div class="panel-heading text-center">
          <h3 class="panel-title">推荐资源</h3>
        </div>
        <div class="panel-body">
          <ul class="list list-group">
            <li v-for="(item, index) in resources" class="list-group-item">
              <a :href="item.link" target="_blank">
                <img class="media-object inline-block " :src="linkIconUrl">
                {{ item.title }}
              </a>
            </li>
          </ul>
        </div>
      </div>

      <div class="panel panel-default corner-radius">
        <div class="panel-body text-center">
          <a href="mailto:xingxingslook@email.com" style="color:#a5a5a5">
            <span style="margin-top: 7px;display: inline-block;">
              <i class="fa fa-heart" style="color: rgba(232, 146, 136, 0.89);"></i>
              建议反馈？请私信 xingxing
            </span>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TheSidebar',
  data() {
    return {
      userIconUrl:require("../../assets/user-icon.png"),
      linkIconUrl:require("../../assets/link-icon.png"),
      slides: [
       
        {
          title: '推荐资源',
          img: require("../../assets/slides-1.jpg"),
          link: ''
        }
      ],
      activeUsers: [], // 活跃用户
      hotArticles: [], // 最热文章
      // 推荐资源
      resources: []
    }
  },
  // 在实例创建完成后
  created() {
    // 通过 axios 执行 GET 请求来返回活跃用户
    this.$axios.get('/apis/actived/users').then((response) => {
      console.log(response.data);
      // 在成功的回调里，从 response.data 获取返回数据
      this.activeUsers = response.data.data
    })
    // 通过 axios 执行 POST 请求来返回七天内最热文章，可以传递 num 来指定返回条数
    this.$axios.post('/articles/hot', { num: 10 }).then((response) => {
      this.hotArticles = response.data
    })
    // 通过 axios 执行 获取推荐资源
    this.$axios.get('/apis/links').then((response) => {
      console.log(response.data);
      // 在成功的回调里，从 response.data 获取返回数据
      this.resources = response.data.data
    }) 

  }
}
</script>

<style scoped>
.carousel-inner img {display:block;margin:auto;line-height:1;width:70%;border:1px solid #ddd;box-shadow:0 0 10px #ccc;-moz-box-shadow:0 0 10px #ccc;-webkit-box-shadow:0 0 10px #ccc;}
</style>


